// Diasumsikan file ini berada di path yang benar sesuai `process.env.SUBMISSION_PATH`
// dan diekspor dengan nama `Galeri` atau sebagai default.

import React, { useState } from 'react';
import { sculptureList } from '@/data/article'; // Impor data yang akan di-mock oleh Jest

export function Galeri() {
  // 1. Gunakan state untuk melacak item mana yang sedang ditampilkan
  const [index, setIndex] = useState(0);

  // 2. Tentukan variabel boolean untuk logika menonaktifkan tombol
  const hasNext = index < sculptureList.length - 1;
  const hasPrev = index > 0;

  // 3. Buat fungsi untuk menangani klik tombol "Selanjutnya"
  function handleNextClick() {
    if (hasNext) {
      setIndex(index + 1);
    }
  }

  // 4. Buat fungsi untuk menangani klik tombol "Sebelumnya"
  function handlePrevClick() {
    if (hasPrev) {
      setIndex(index - 1);
    }
  }

  // Ambil objek patung saat ini berdasarkan state `index`
  let sculpture = sculptureList[index];

  // 5. Render JSX secara dinamis
  return (
    <>
      {/* Tombol dengan event handler dan atribut `disabled` dinamis */}
      <button onClick={handlePrevClick} disabled={!hasPrev}>
        Artikel Sebelumnya
      </button>
      <button onClick={handleNextClick} disabled={!hasNext}>
        Artikel Selanjutnya
      </button>

      {/* Tampilan data yang berubah sesuai state */}
      <h2>
        <i>{sculpture.name}</i> oleh {sculpture.artist}
      </h2>
      <h3>
        ({index + 1} dari {sculptureList.length})
      </h3>
      <img
        src={sculpture.url}
        alt={sculpture.alt}
      />
      <p>
        {sculpture.description}
      </p>
    </>
  );
}

